
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import brain from '../../images/brain.jpg';
import natours from '../../images/natours.jpg';
import wog from '../../images/wog3.png';
import crownClothing from '../../images/crown-clothing4.png';

import ModalVideo from 'react-modal-video';

import '../../css/project.scss';

class Project extends Component {
    constructor(){
        super();
        
        this.state = {
            isOpen: false,
            id: '',
            Github: {
                UClothingRepoUrl: 'https://github.com/JonHualde/Uclothing',
                smartBrain: {
                    frontEndRepoUrl: 'https://github.com/JonHualde/SmartBrain',
                    backEndRepoUrl: 'https://github.com/JonHualde/Smartbrain-API'
                },
                outdoorsRepoUrl: 'https://github.com/JonHualde/Outdoors'
            },
            Website: {
                UClothingWebsite: 'https://uclothing.herokuapp.com/',
                smartbrainWebsite: 'https://smart-brain-jon.herokuapp.com/',
                workingOnTheGlobeWebsite: 'http://wog.mikecodeur.com/',
                outdoorsWebsite: 'https://jonhualde.github.io/Outdoors/'
            }
        }
    }

    goTo(link) {
        window.open(link)
    }

    openModal () {
        this.setState({ isOpen: true })
    }

    render() {
    const { Github, Website } = this.state;
    const { history } = this.props;

    return (
    <React.Fragment>
        <ModalVideo 
            channel='youtube' 
            isOpen={this.state.isOpen} 
            videoId={this.state.id} 
            onClose={ () => this.setState({ isOpen: false, id: '' }) }
        />
        <section className="section" id="projects">
            <div className="container">
                <div className="row">
                    <div className="col-lg-8 offset-lg-2">
                        <div className="about-title mx-auto text-center">
                            <h2 className="font-weight-light">
                                Completed &amp; ongoing projects 
                            </h2>
                            <p className="text-muted pt-4">
                                <strong>
                                HOVER on a project or CLICK on FULL DESCRIPTION to get more context.
                                </strong><br/><br/>
                                Explanatory videos, repositories and websites available!
                            </p>
                        </div>
                    </div>
                </div>
                <div className="row margin-t-40">
                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                                <p id='fullDescription-text' onClick={ () => history.push('/U-Clothing')}>
                                FULL DESCRIPTION HERE
                                </p>
                                <div className="container-member">
                                    <img alt="project-image" src={crownClothing} 
                                         className="img-fluid rounded" 
                                         id="image-project-image"
                                    />
                                    <div id="image-project-caption">
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Github.UClothingRepoUrl)}
                                        >
                                            Github 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={ () => { this.setState({ id: 'xiKrSrwvmJg'}) ; this.openModal() }}    
                                        >
                                            Video 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Website.UClothingWebsite)}
                                        >
                                            Go to website 
                                        </figcaption> 
                                    </div>
                                </div>
                            </div>
                            <h4 className="team-name">
                                U-CLOTHING 
                            </h4>
                            <p className="text-uppercase team-designation">
                                Online clothing store, with authentication + payment included
                            <br/>
                            <strong> 
                                MADE WITH React/Redux-Saga/Firebase/GraphQl
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Available, project still ongoing  &#00;
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p> 
                            {/* pe-7s-clock */}
                        </div>
                    </div>

                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                                <p id='fullDescription-text'onClick={ () => history.push('/Smart-Brain')}>
                                FULL DESCRIPTION HERE
                                </p>
                                <div className="container-member">
                                        <img alt="" src={brain} 
                                             className="img-fluid rounded" 
                                             id="image-project-image"
                                        />
                                        <div id="image-project-caption__smart">
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.smartBrain.frontEndRepoUrl)}
                                            >
                                                FrontEnd 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.smartBrain.backEndRepoUrl)}
                                            >
                                                BackEnd 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={ () => { this.setState({ id: 'PdErGBSygrY'}) ; this.openModal() }}
                                            >
                                                Video 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Website.smartbrainWebsite)}
                                            >
                                                Go to website 
                                            </figcaption> 
                                        </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                SMARTBRAIN
                            </h4>
                            <p className="text-uppercase team-designation">
                                Face recognition web app calling clarify API 
                            <br/>
                            <strong> 
                                Made with React/Node.js/PostgreSQL
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Available &#00;  
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p>
                        </div>
                    </div>

                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                            <p id='fullDescription-text' onClick={ () => history.push('/W.O.G')}>
                            FULL DESCRIPTION HERE
                            </p>
                                <div className="container-member">
                                    <img alt="" src={wog} 
                                            className="img-fluid rounded" 
                                            id="image-project-image"
                                    /> 
                                    <div id="image-project-caption">
                                        <figcaption id="Link-text" 
                                        onClick={ () => { this.setState({ id: 'x-e92PZQB_8'}) ; this.openModal() }}>
                                            Video 
                                        </figcaption>
                                        <figcaption id="Link-text"
                                            onClick={() => this.goTo(Website.workingOnTheGlobeWebsite)}
                                        >
                                            Go to website 
                                        </figcaption> 
                                    </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                WORKING ON THE GLOBE
                            </h4>
                            <p className="text-uppercase team-designation">
                                Video plateform for entrepreneurs and digital nomads
                                <br/>
                                <strong> 
                                    Made with React/Redux-saga/GraphQl
                                </strong>
                                <br/>
                                <strong style={{ color: 'green'}}>
                                    Available, project still being updated &#00;
                                </strong>
                                <i className="pe-7s-like2 text-custom"></i>
                            </p>
                        </div>
                    </div>

                    <div className="col-lg-3 col-sm-6">
                        <div className="team-box text-center">
                            <div className="team-wrapper">
                            <p id='fullDescription-text' onClick={ () => history.push('/Outdoors')}>
                            FULL DESCRIPTION HERE
                            </p>
                                <div className="container-member">
                                        <img alt="" src={natours} 
                                             className="img-fluid rounded"
                                             id="image-project-image" 
                                        /> 
                                        <div id="image-project-caption">
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Github.outdoorsRepoUrl)}
                                            >
                                                Github 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={ () => { this.setState({ id: 'oYW7biVSSM0'}) ; this.openModal() }}
                                            >
                                                Video 
                                            </figcaption>
                                            <figcaption id="Link-text"
                                                onClick={() => this.goTo(Website.outdoorsWebsite)}
                                            >
                                                Go to website 
                                            </figcaption> 
                                        </div> 
                                </div>
                            </div>
                            <h4 className="team-name">
                                NATOURS
                            </h4>
                            <p className="text-uppercase team-designation">
                                Designed for tours agencies with stunning effects, made with advanced CSS
                            <br/>
                            <strong> 
                                MADE WITH HTML/CSS with SASS/JAVASCRIPT
                            </strong>
                            <br/>
                            <strong style={{ color: 'green'}}>
                                Available &#00;
                            </strong>
                            <i className="pe-7s-like2 text-custom"></i></p>
                        </div>
                    </div>

                </div>
            </div>
        </section> 
    </React.Fragment>
        );
    }
}

export default withRouter(Project);


